import React from "react";
import {Field, reduxForm} from "redux-form";
import {required} from "../../../Utils/validators/validators";
import {Input, Textarea} from "../../common/FormsControls/FormsControls";
import classes from './ContentInfo.module.css';
import style from "../../common/FormsControls/FormsControls.module.css";

const ProfileDataForm = ({handleSubmit, profile, error}) => {
    return <form onSubmit={handleSubmit} >
        <div >
        <div > <button>save</button> </div>
        {error && <div className={style.formSummaryError}> {error} </div>} {/*если в пропсах есть ошибка то мы покажем props.error*/}
        <div >
            <Field placeholder={"Full name"} name={"fullName"} validate={[required]}  component={Input} /> {/*используем кастомный инпут*/}
        </div>
        <div>
            <b>Looking for a job</b> :
            <Field type={"checkbox"} name={"lookingForAJob"} component={Input}/>
        </div>
        <div>
            <b>My professional skills</b> :
            <Field placeholder={"My professional skills"} name={"lookingForAJobDescription"} validate={[required]}  component={Textarea}/>
        </div>
        <div>
            <b>About me</b> :
            <Field placeholder={"About me"} name={"aboutMe"} validate={[required]}  component={Textarea}/>
        </div>
        <div>
            <b>Contacts</b> : {Object.keys(profile.contacts).map(key => {
            return <div key={key} className={classes.contact}>
            <b>{key}: <Field placeholder={key} name={"contacts." + key} component={Input}/></b>
            </div>
        })} {/*Object.keys пробегается по объекту contacts и мы мапим компоненту Contact на базе каждого значения*/}
        </div>
        </div>
    </form>
}

const ProfileDataFormReduxForm = reduxForm({form: 'edit-profile'}) (ProfileDataForm)

export default ProfileDataFormReduxForm;