import React from 'react';
import classes from "./startGame.module.css";
import {NavLink} from "react-router-dom";

const StartGame = () => {
    return (<div>
        <div >
            <img  className={classes.gamePhoto1}/>
        </div>
        <div className={classes.gameTextPadding}>
            <h3> Привет друг! Не хочешь сыграть в одну игру? </h3>
        </div>

        <div className={classes.buttonLeft01} >
            <NavLink to="/profile" className={navData => navData.isActive ? classes.active : classes.item}>Отказаться</NavLink>
        </div>
        <div className={classes.buttonLeft02} >
            <NavLink to='/game/1' className={navData => navData.isActive ? classes.active : classes.item}>Согласиться!</NavLink>
        </div>
    </div>)
}

export default StartGame;