import React, {useState} from 'react';
import classes from './ContentInfo.module.css';
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import userPhoto from "../../../assets/images/user4.jpg";
import headerPhoto from "../../../assets/images/9090999090.jpg";
import ProfileDataForm from "./ProfileDataForm";


const ContentInfo = (props) => {

    let [editMode, setEditMode] = useState(false); /*с помощью хука создоем LocalState для editMode*/

    if (!props.profile) {
        return <Preloader/>
    }

    const onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
            props.savePhoto(e.target.files[0])
        }
    }

    const onSubmit = (formData) => {
         props.saveProfile(formData).then(
             () => {setEditMode(false);}
         ) /*при нажатии на кнопку save editMode меняется на false и режим редактирования выключается*/
    }

    return (
        <div>

            <div>
                <img src={headerPhoto} className={classes.headerPhoto}/>
            </div>
            <div className={classes.paddingStatus}>
                <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>
            </div>
            <div className={classes.descriptionBlock}>


                { editMode ? <ProfileDataForm initialValues={props.profile} profile={props.profile} onSubmit={onSubmit}/>
                    : <div className={classes.profile}> <ProfileData  goToEditMode={ () => {setEditMode(true)}} profile={props.profile} isOwner={props.isOwner} /> </div> }

                <img src={props.profile.photos.large || userPhoto} className={classes.mainPhoto}/>
                {props.isOwner && <input type={"file"} onChange={onMainPhotoSelected}/>}

            </div>
        </div>
    )
}

const ProfileData = (props) => {
    return <div>
        {props.isOwner && <div className={classes.buttonEdit}> <button onClick={props.goToEditMode}>edit</button> </div>}
        <div className={classes.fullName}>
            <b>Full name</b> : {props.profile.fullName}
        </div>
        <div>
            <b>Looking for a job</b> : {props.profile.lookingForAJob ? "yes" : "no"}
        </div>
        {props.profile.lookingForAJob &&
        <div>
            <b>My professional skills</b> : {props.profile.lookingForAJobDescription}
        </div>}
        <div>
            <b>About me</b> : {props.profile.aboutMe}
        </div>
        <div>
            <b>Contacts</b> : {Object.keys(props.profile.contacts).map(key => {
            return <Contact key={key} contactTitle={key} contactValue={props.profile.contacts[key]}/>
        })} {/*Object.keys пробегается по объекту contacts и мы мапим компоненту Contact на базе каждого значения*/}
        </div>
    </div>
}


const Contact = ({contactTitle, contactValue}) => {
    return <div className={classes.contact}><b>{contactTitle}</b> : {contactValue} </div>
}

export default ContentInfo;