import React from 'react';
import classes from "./startGame.module.css";
import {NavLink} from "react-router-dom";

const Chapter = (props) => {

    debugger;
    return (
        <div>
            <div>
                <div>
                    <img className={classes.gamePhoto1} src={props.photo}/>
                </div>
                <div className={classes.gameTextPadding}>
                    <h2>{props.text}</h2>
                </div>
            </div>
            <div className={classes.buttonLeft01}>
                <NavLink to={"/game/" + props.step1}
                         className={navData => navData.isActive ? classes.active : classes.item}>{props.choice1}</NavLink>
            </div>
            <div className={classes.buttonLeft02}>
                <NavLink to={"/game/" + props.step2}
                         className={navData => navData.isActive ? classes.active : classes.item}>{props.choice2}</NavLink>
            </div>
            <div className={classes.buttonLeft03}>
                <NavLink to={"/profile" + props.step3}
                         className={navData => navData.isActive ? classes.active : classes.item}>{props.choice3}</NavLink>
            </div>
            <div className={classes.endPage}>
                .
            </div>

        </div>
    )
}

export default Chapter;